<template>
  <el-form ref="form" :model="form" label-width="300px" label-position="top">
    <el-form-item label="账号">
      <el-input v-model="form.act" placeholder="手机号 / 学号"></el-input>
    </el-form-item>
    <el-form-item label="密码">
      <el-input v-model="form.psw"></el-input>
    </el-form-item>
    <el-form-item label="请选择身份">
      <el-radio-group v-model="form.choice">
        <el-radio label="教师"></el-radio>
        <el-radio label="学生"></el-radio>
        <el-radio label="管理员"></el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="go">登录</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  data () {
    return {
      form: {
        act: '',
        psw: '',
        choice: ''
      }
    }
  },
  methods: {
    go() {
      // alert(this.form.choice);
      if(this.form.choice === '学生'){
        this.$router.push('/student');
      }
      else if(this.form.choice === '教师'){
        this.$router.push('/teacher');
      }
      else{
        this.$router.push('/administrator');

      }
    }
  }
}
</script>

<style>
.el-input {
  width: 300px;
}

.el-form {
  margin: 180px auto 0 auto;
}
</style>



